/*  src/Title.css  */
.Title {
  box-sizing: border-box;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: baseline;
  padding: 1em;
  margin-bottom: 2em;
  background-color: rgb(192, 45, 26);
  color: #fff;
}

.Title-Subtitle {
  color: rgba(255, 255, 255, 0.75);
}

.Title .nav-link-active {
  color: #fff;
  border-bottom: 2px solid;
}